body {
  background-color: #614385;
  background-image: linear-gradient(to left, #fd746c, #ff9068);
  color: #727272;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 16px;
  font-weight: 300;
  text-align: center; }

a {
  color: #2DC0BD;
  text-decoration: none;
  transition: color ease-in-out .4s; }
  a:hover {
    color: #239794; }

p {
  font-size: 1em;
  font-weight: 300; }

h1 {
  font-family: Source Code Pro;
  font-weight: 300;
  margin: 0;
  text-transform: uppercase; }

.flex {
  align-items: center;
  display: flex; }
  .flex__col--1 {
    flex-basis: 30%;
    flex-grow: 1; }
  .flex__col--2 {
    flex-basis: 40%;
    flex-grow: 2; }

.container {
  margin: 0 auto; }
  @media (min-width: 480px) {
    .container {
      max-width: 768px; } }

.app {
  background-color: white;
  border-radius: 3px;
  box-shadow: -3px 6px 10px rgba(0, 0, 0, 0.3);
  height: 100vh;
  position: relative;
  text-align: center; }
  @media (min-width: 768px) {
    .app {
      height: calc(100vh - 2em);
      margin-top: 1em; } }

.head {
  background-color: #2e3240;
  background-image: linear-gradient(30deg, #444a5d, #191b22);
  color: #fff;
  height: 10vh; }

.chat {
  height: 90vh; }
  @media (min-width: 768px) {
    .chat {
      height: calc(90vh - 2em); } }
  .chat .msgs {
    height: 90%;
    max-height: 90%;
    overflow: auto; }
  .chat .msg {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    padding: 0 1em; }
    .chat .msg li {
      border-radius: 2px;
      margin-bottom: 5px;
      padding: .725em 1em; }
  .chat .msg--received li {
    align-self: flex-start;
    background-color: rgba(215, 215, 221, 0.1);
    border: 1px solid rgba(215, 215, 221, 0.4);
    text-align: left; }
  .chat .msg--sent li {
    align-self: flex-end;
    background-color: rgba(105, 105, 150, 0.1);
    border: 1px solid rgba(105, 105, 150, 0.15);
    text-align: right; }
  .chat .box {
    border-radius: 0 0 3px 3px;
    bottom: 0;
    height: 10%;
    max-height: 55px;
    min-height: 40px;
    position: absolute;
    text-align: left;
    width: 100%; }
    .chat .box__input {
      background-color: #3f3f5a;
      border: 0;
      border-radius: 0 0 3px 3px;
      color: #D7D7DD;
      height: 100%;
      padding: 1em;
      width: 100%; }
    .chat .box__input:focus {
      outline: none; }
    .chat .box button {
      background-color: #696996;
      border: 0;
      border-radius: 0 0 3px;
      color: #fff;
      font-size: 1.2em;
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      font-weight: 600;
      height: 100%;
      position: absolute;
      right: 0;
      top: 0;
      width: 100px; }

.nav {
  padding-left: 1em;
  text-align: left; }

.title {
  flex-grow: 2; }
  .title h1 {
    font-size: 1.5em;
    line-height: 10vh; }

.account {
  padding-right: 1em;
  text-align: right; }
  .account .sign {
    cursor: pointer; }
